import React, {useState, useEffect} from 'react'
import './MiniCart.scss'
import miniQuit from '../../medias/miniQuit.svg';

function MiniCart({
    onClick,
    message,
    name,
    cover,
    miniCartOpen,
    setMiniCartOpen
}) {

    const [position,
        setPosition] = useState('-100%')
    const [opacity,
        setOpacity] = useState('0')

    useEffect(() => {
        miniCartOpen && setTimeout(() => {
            setOpacity('0.5')
            setPosition('0%')
        }, 0);

    }, [miniCartOpen])

    function closePanel() {
        setOpacity('0')
        setPosition('-100%')
        setTimeout(() => {
            setMiniCartOpen(false)
        }, 300);
    }

    return (miniCartOpen
        ? <div
                style={{
                background: `rgba(0,0,0,${opacity})`
            }}
                className="mini-cart">
                <div
                    className="mini-cart-container"
                    style={{
                    bottom: position
                }}>
                    <div className="title">
                        <p>Added to cart</p>
                        <img
                            className="icon"
                            onClick={closePanel}
                            src={miniQuit}
                            alt="quit"/>
                    </div>

                    <img src={cover} alt={cover}/>
                </div>
            </div>
        : null)
}

export default MiniCart