import React, {useState, useEffect} from 'react'
import Header from './header/Header'
import ShoppingList from './shopping-list/ShoppingList'
import Footer from './footer/Footer'
import './App.scss'

function App() {

    const savedCart = localStorage.getItem('cart')

    const [cart,
        setCart] = useState(savedCart
        ? JSON.parse(savedCart)
        : [])

    const [isAdded,
        setIsAdded] = useState(false)

    const [isPlp,
        setIsPlp] = useState(true)

    

    useEffect(() => {
        localStorage.setItem('cart', JSON.stringify(cart))
    }, [cart])

    return (
        <div>
            <Header
                cart={cart}
                setCart={setCart}
                isAdded={isAdded}
                setIsAdded={setIsAdded}
                setIsPlp={setIsPlp}/>
            <ShoppingList
                cart={cart}
                setCart={setCart}
                isAdded={isAdded}
                setIsAdded={setIsAdded}
                isPlp={isPlp}
                setIsPlp={setIsPlp}/>
            <Footer/>
        </div>
    );
}

export default App;
